/* Dashboard specific */
.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.stat-card {
  background: white;
  border-radius: var(--border-radius);
  padding: 1.5rem;
  text-align: center;
  box-shadow: var(--shadow);
  border-left: 4px solid var(--primary-color);
  transition: transform 0.3s;
}

.stat-card:hover {
  transform: translateY(-5px);
}

.stat-card h3 {
  margin: 0;
  font-size: 2rem;
  color: var(--primary-color);
}

.stat-card p {
  margin: 0.5rem 0 0 0;
  color: var(--muted-text);
}

.dashboard-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

@media (max-width: 768px) {
  .dashboard-content {
    grid-template-columns: 1fr;
  }
}

/* Loading spinner */
.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid var(--primary-color);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 2rem auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* Medicine page styles */
.medicines-page .content-grid {
  grid-template-columns: 1fr 1fr 2fr;
}

.medicines-page .alerts-card {
  background: linear-gradient(135deg, #fff3cd 0%, #ffeaa7 100%);
  border: 1px solid #f39c12;
}

.medicines-page .alerts-section .alert {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 0.9rem;
}

.medicines-page .low-stock-alert {
  background: #ffe6e6;
  border-left: 4px solid #e74c3c;
  color: #c0392b;
}

.medicines-page .expiring-alert {
  background: #fff3cd;
  border-left: 4px solid #f39c12;
  color: #e67e22;
}

.medicines-page .filters {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.medicines-page .filters select {
  padding: 0.5rem;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  background: white;
}

.medicines-page .alert-row {
  background: #fff5f5;
  border-left: 3px solid #e74c3c;
}

.medicines-page .low-stock {
  color: #e74c3c;
  font-weight: bold;
}

.medicines-page .expiring-soon {
  color: #f39c12;
  font-weight: bold;
}

/* Admin page specific */
.admin-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.section-tabs {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.section-tabs .btn {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
}

@media (max-width: 600px) {
  .section-tabs {
    flex-direction: column;
    width: 100%;
  }

  .admin-controls {
    flex-direction: column;
    align-items: stretch;
  }
}

/* Badge styles */
.badge {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 600;
  display: inline-block;
}

.badge.success {
  background-color: #d4edda;
  color: #155724;
}

.badge.warning {
  background-color: #fff3cd;
  color: #856404;
}

.badge.danger {
  background-color: #f8d7da;
  color: #721c24;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal {
  background: white;
  padding: 2rem;
  border-radius: var(--border-radius);
  width: 90%;
  max-width: 500px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 1rem;
}

.modal-header h3 {
  margin: 0;
  color: var(--primary-color);
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--muted-text);
}

.modal-body {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 2rem;
  border-top: 1px solid var(--border-color);
  padding-top: 1rem;
}
/* Sidebar Toggle Button */
.sidebar-toggle {
  position: fixed;
  top: 1rem;
  left: 1rem;
  z-index: 200;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 50%;
  width: 45px;
  height: 45px;
  font-size: 1.5rem;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sidebar-toggle:hover {
  background: #0056b3;
  transform: scale(1.1);
}

/* Improved Form Layouts - No Scrolling Required */
.content-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  max-height: calc(100vh - 200px);
}

.medicines-page .content-grid {
  grid-template-columns: 1fr;
}

.form-card {
  background: linear-gradient(135deg, #f0f8ff, #e6f7ff);
  max-height: calc(100vh - 250px);
  overflow-y: auto;
  position: sticky;
  top: 0;
}

.list-card {
  overflow-x: auto;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
}

/* Compact Form Styles */
.patient-form, .doctor-form, .appointment-form, .medicine-form, .prescription-form, .bill-form, .staff-form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.75rem;
}

.patient-form label, .doctor-form label, .appointment-form label, .medicine-form label, .prescription-form label, .bill-form label, .staff-form label {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.patient-form .full-width, .doctor-form .full-width, .appointment-form .full-width, .medicine-form .full-width, .prescription-form .full-width, .bill-form .full-width, .staff-form .full-width {
  grid-column: 1 / -1;
}

.patient-form textarea, .doctor-form textarea, .appointment-form textarea, .medicine-form textarea, .prescription-form textarea, .bill-form textarea, .staff-form textarea {
  grid-column: 1 / -1;
}

.patient-form .form-actions, .doctor-form .form-actions, .appointment-form .form-actions, .medicine-form .form-actions, .prescription-form .form-actions, .bill-form .form-actions, .staff-form .form-actions {
  grid-column: 1 / -1;
}

/* Compact inputs */
input, select, textarea {
  padding: 0.5rem;
  font-size: 0.95rem;
}

label {
  font-size: 0.9rem;
  margin-bottom: 0;
}

/* Better scrollbars */
.form-card::-webkit-scrollbar, .list-card::-webkit-scrollbar, .table-wrap::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.form-card::-webkit-scrollbar-track, .list-card::-webkit-scrollbar-track, .table-wrap::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.form-card::-webkit-scrollbar-thumb, .list-card::-webkit-scrollbar-thumb, .table-wrap::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 10px;
}

.form-card::-webkit-scrollbar-thumb:hover, .list-card::-webkit-scrollbar-thumb:hover, .table-wrap::-webkit-scrollbar-thumb:hover {
  background: #0056b3;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .sidebar-toggle {
    top: 0.5rem;
    left: 0.5rem;
  }
  
  .patient-form, .doctor-form, .appointment-form, .medicine-form, .prescription-form, .bill-form, .staff-form {
    grid-template-columns: 1fr;
  }
  
  .main-content {
    padding: 1rem;
  }
}

/* Page layout improvements */
.page {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--shadow);
  padding: 1.5rem;
  margin-bottom: 1rem;
  max-height: calc(100vh - 100px);
  overflow-y: auto;
}

.hero {
  text-align: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid var(--primary-color);
}

.hero h1 {
  color: var(--primary-color);
  margin-bottom: 0.5rem;
  font-size: 2rem;
}

.hero-sub {
  color: var(--muted-text);
  font-size: 1rem;
}
